body {
  box-sizing: border-box;
  min-width: 320px;
  font-family: "Poppins", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 130%;
  color: #1d1613; }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto; }

.container {
  width: 100%;
  padding-left: calc(50% - (1366px / 2) + 98px);
  padding-right: calc(50% - (1366px / 2) + 98px); }
  @media (max-width: 1366px) {
    .container {
      padding-left: 98px;
      padding-right: 98px; } }
  @media (max-width: 1023px) {
    .container {
      padding-left: 45px;
      padding-right: 45px; } }
  @media (max-width: 767px) {
    .container {
      padding-left: 15px;
      padding-right: 15px; } }

.visually-hidden {
  position: absolute;
  z-index: -1;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: none;
  clip: rect(0 0 0 0); }

.burger {
  display: none; }
  @media (max-width: 1023px) {
    .burger {
      display: block;
      width: 25px;
      height: 18px;
      padding: 0;
      border: 0;
      background-color: transparent;
      background-image: url("../img/burger-icon.svg");
      background-repeat: no-repeat;
      cursor: pointer; } }

.burger--white {
  background-image: url("../img/burger-white-icon.svg"); }

.search {
  margin-top: 12px; }

.search__form input {
  padding-left: 29px;
  border: none;
  font-size: 14px;
  font-weight: 500;
  color: #9b9997;
  background-image: url("../img/loupe-icon.svg");
  background-size: 14px 15px;
  background-repeat: no-repeat;
  background-position: left center; }
  @media (max-width: 1023px) {
    .search__form input {
      width: 100%;
      padding: 12px;
      padding-left: 40px;
      border-radius: 21px;
      color: #ffffff;
      background-image: url("../img/loupe-white-icon.svg");
      background-size: 16px 16px;
      background-color: rgba(255, 255, 255, 0.15);
      background-position: left 12px center; }
      .search__form input::placeholder {
        color: transparent; } }

.logo {
  display: flex; }

.logo__link {
  display: block; }

.logo__img {
  display: block;
  width: 66px;
  height: 66px;
  fill: #a87b62; }
  @media (max-width: 767px) {
    .logo__img {
      width: 52px;
      height: 52px; } }
  @media (max-width: 1023px) {
    .page-header--menu-open .logo__img {
      fill: #ffffff; } }

.login-header__link {
  margin-left: -1px;
  font-size: 14px;
  font-weight: 500;
  line-height: normal; }
  @media (max-width: 1023px) {
    .login-header__link {
      margin-left: 0;
      font-size: 18px;
      line-height: 130%;
      color: #ffffff; } }

.cart-header__link {
  display: block;
  font-size: 14px;
  font-weight: 500;
  line-height: normal;
  cursor: pointer; }
  @media (max-width: 1023px) {
    .cart-header--img .cart-header__link {
      height: 18px;
      width: 20px;
      background-image: url("../img/cart-icon.svg"); } }
  @media (max-width: 1023px) {
    .page-header--menu-open .cart-header--img .cart-header__link {
      background-image: url("../img/cart-white-icon.svg"); } }

@media (max-width: 1023px) {
  .cart-header--img .cart-header__text,
  .cart-header--img .cart-header__counter {
    position: absolute;
    z-index: -1;
    width: 1px;
    height: 1px;
    margin: -1px;
    padding: 0;
    overflow: hidden;
    border: none;
    clip: rect(0 0 0 0); } }

.main-menu {
  display: flex;
  justify-content: center; }
  @media (max-width: 1023px) {
    .main-menu {
      display: none; } }

@media (max-width: 1023px) {
  .main-menu--show {
    display: block; } }

.main-menu__list {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  margin-left: 45px; }
  @media (max-width: 1023px) {
    .main-menu__list {
      display: block;
      margin-left: 0; } }

.main-menu__item {
  margin-right: 35px; }
  @media (max-width: 1023px) {
    .main-menu__item {
      margin-right: 0;
      margin-bottom: 40px; }
      .main-menu__item:last-child {
        margin-bottom: 0; } }

.main-menu__link {
  font-weight: 500;
  color: #1d1613; }
  @media (max-width: 1023px) {
    .main-menu__link {
      font-size: 24px;
      line-height: 36px;
      color: #ffffff; } }

.small-menu__list {
  list-style-type: none;
  margin: 0;
  padding: 0; }

.small-menu__item {
  margin-bottom: 30px; }
  .small-menu__item:last-child {
    margin-bottom: 0; }

.small-menu__link {
  font-weight: 500;
  font-size: 18px;
  color: rgba(255, 255, 255, 0.75); }

.button {
  display: inline-block;
  min-width: 180px;
  padding-left: 0;
  padding-right: 0;
  padding-top: 14px;
  padding-bottom: 13px;
  border: 1px solid #ffffff;
  border-radius: 5px;
  font-family: "Cormorant Garamond", "Times New Roman", sans-serif;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  text-align: center;
  cursor: pointer; }
  .button:hover, .button:focus {
    opacity: 0.7; }

.button--transparent-white {
  background-color: transparent;
  color: #ffffff; }

.button--transparent-blue {
  border-color: #a87b62;
  background-color: transparent;
  color: #a87b62; }

.button--fill-blue {
  border: none;
  background-color: #a87b62;
  color: #ffffff; }

.site-wrapper {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  overflow-x: hidden; }

.page-header {
  width: 100%;
  padding-left: calc(50% - (1366px / 2) + 98px);
  padding-right: calc(50% - (1366px / 2) + 98px);
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  padding-top: 30px;
  padding-bottom: 30px; }
  @media (max-width: 1366px) {
    .page-header {
      padding-left: 98px;
      padding-right: 98px; } }
  @media (max-width: 1023px) {
    .page-header {
      padding-left: 45px;
      padding-right: 45px; } }
  @media (max-width: 767px) {
    .page-header {
      padding-left: 15px;
      padding-right: 15px; } }
  @media (max-width: 767px) {
    .page-header {
      padding-top: 20px;
      padding-bottom: 20px; } }

.page-header--menu-open {
  background-color: #a87b62; }

.page-header__burger {
  margin-top: 24px; }
  @media (max-width: 767px) {
    .page-header__burger {
      margin-top: 17px; } }
  @media (max-width: 1023px) {
    .page-header--menu-open .page-header__burger {
      order: 1; } }

.page-header__search {
  width: 200px; }
  @media (max-width: 1023px) {
    .page-header__search {
      display: none; } }
  @media (max-width: 1023px) {
    .page-header--menu-open .page-header__search {
      order: 4;
      display: block;
      width: 100%; } }

.page-header__logo {
  margin-left: calc(50% - 233px);
  margin-right: auto;
  min-width: 66px;
  width: 66px;
  height: 66px; }
  @media (max-width: 1023px) {
    .page-header__logo {
      margin-left: calc(50% - 58px); } }
  @media (max-width: 767px) {
    .page-header__logo {
      margin-left: calc(50% - 51px); } }
  @media (max-width: 1023px) {
    .page-header--menu-open .page-header__logo {
      order: 2; } }

.page-header__login {
  margin-top: 12px;
  margin-left: auto;
  margin-right: 38px; }
  @media (max-width: 1023px) {
    .page-header__login {
      display: none; } }
  @media (max-width: 1023px) {
    .page-header--menu-open .page-header__login {
      order: 7;
      display: block;
      margin-top: 0;
      margin-left: 0;
      margin-right: 0; } }

.page-header__cart {
  margin-top: 12px; }
  @media (max-width: 1023px) {
    .page-header__cart {
      margin-top: 24px;
      margin-right: -1px; } }
  @media (max-width: 767px) {
    .page-header__cart {
      margin-top: 17px; } }
  @media (max-width: 1023px) {
    .page-header--menu-open .page-header__cart {
      order: 3; } }

.page-header__menu {
  width: 100%;
  margin-top: 39px; }
  @media (max-width: 1023px) {
    .page-header--menu-open .page-header__menu {
      order: 5;
      margin-top: 55px;
      margin-bottom: 55px; } }
  @media (max-width: 767px) {
    .page-header--menu-open .page-header__menu {
      margin-top: 45px;
      margin-bottom: 45px; } }

.page-header__small-menu {
  display: none; }
  @media (max-width: 1023px) {
    .page-header--menu-open .page-header__small-menu {
      order: 6;
      display: block;
      width: 100%;
      margin-bottom: 45px; } }

.page-header--absolute {
  position: absolute; }
